button {
  width: 100px;
  height: auto;
  padding: 5px;
  margin-right: 10px;
  font-size: 16px;
  border: 1px solid black;
  border-radius: 5px;
  background-color: rgb(200, 255, 255);
  cursor: pointer;
}

button:hover {
  background: rgb(255, 255, 240);
  border: 1px solid steelblue;
}
